
.row {text-align: center;justify-content: center;margin-top: 1%; }
.col-4 {border: solid whitesmoke thin;
      margin-top: 1%;
      justify-content: center;
      
      }

.card {display: flex; 
      flex-direction: column; 
      justify-content: center;
       align-items: center;}      


.movieViewContainer {border: solid whitesmoke thin;
                     margin-top: 1%;
                     
                        }


.col-12, col {text-align: center;}

.bigPoster {text-align: center;}
.bigPoster img { width: 250px; height: 350px;}

.movieInfo {text-align: right;}

.button {cursor: pointer;}
.previewImg {width: 80%; height: 60%;}
.button_card{width: 50%;
            background-color: blue; color: whitesmoke;
            border: none;
                  }

.movieViewContainer button{
      
      width: 100px;
